import React from 'react'
import PostsForm from './PostsForm';

const Posts = ({posts, changePosts, title, changeTitle, text, changeText, author, changeAuthor}) => {

    return(
        <>
                <PostsForm 
                    posts={posts} 
                    changePosts={changePosts}
                    title={title}
                    changeTitle={changeTitle}
                    text={text}
                    changeText={changeText}
                    author={author}
                    changeAuthor={changeAuthor}
                />
            {posts.length > 0 ?
                posts.map((post, index) => {
                    return(
                        <div key={index} id="posts__container" className="posts__container">
                            <h3>{post.title}</h3>
                            <p>{post.date}</p>
                            <h6>{post.author}</h6>
                            <p>{post.text}</p>
                        </div>
                    )
                })
                :
                <h1>
                    There are no posts here!
                    You can create one whenever you want to.
                </h1>
            }
        </>
    )
}


export default Posts;