import React from 'react'
import styled from 'styled-components'
import { v4 as uuidv4  } from 'uuid'


const PostsForm = ({posts, changePosts, title, changeTitle, text, changeText, author, changeAuthor}) => {

        const handleOnSubmit = (e) => {
            e.preventDefault()
            if(title.length === 0 || text.length === 0 || author.length === 0){
                return;
            }
                    changePosts([...posts,
                        {
                            title: title,
                            text: text,
                            author: author,
                            date: Date(),
                            id: uuidv4()
                        }
                    ])
            changeTitle('')
            changeText('')
            changeAuthor('')
        }

        const handleOnChange = (e) => {
            switch(e.target.name){
                case 'title': 
                    changeTitle(e.target.value)
                    break;
                case 'text':
                    changeText(e.target.value)
                    break;
                case 'author':
                    changeAuthor(e.target.value)
                    break;
                default:
                    alert('Valor no encontrado')
            }
        }


    return(
        <div>
            <StyledForm action="" onSubmit={handleOnSubmit}>               
                <InputFormContainer>
                    <label htmlFor="title">Title: </label>
                    <input 
                        type="text"
                        name="title" 
                        id="title"
                        value={title}                        
                        onChange={handleOnChange}
                    />
                </InputFormContainer>
                <InputFormContainer>
                    <label htmlFor="text">Text: </label>
                    <input 
                        type="text" 
                        name="text"
                        id="text"
                        value={text}
                        onChange={handleOnChange}
                    />
                </InputFormContainer>
                <InputFormContainer>
                    <label htmlFor="author">Author: </label>
                    <input 
                        type="text" 
                        name="author"
                        id="author"
                        value={author}
                        onChange={handleOnChange}
                    />
                        <br />

                    <button className="submit__button" type="submit">                        
                        Upload Post
                    </button>
                </InputFormContainer>    
            </StyledForm>
        </div>
    )
}

const StyledForm = styled.form`
    display: flex;
    flex-direction: column;
`
const InputFormContainer = styled.div`
    margin: 10px;
`

export default PostsForm